.App {
  text-align: center;
}

.App-logo {
  height: 40vmin;
  pointer-events: none;
}

@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    animation: App-logo-spin infinite 20s linear;
  }
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

@keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}
.chart-container {
  position: relative;
  height: 300px;
  width: 100%;
  max-width: 1200px;
  margin: 0 auto;
}

.chart-container canvas {
  width: 100% !important;
  height: 100% !important;
}
.container {
  width: 100%;
  max-width: 1200px; /* Match the table's min-width to create overflow */
  margin: 0 auto;
}
.tracker-table {
  min-width: 1200px; /* Ensure the table is wide enough to overflow */
}
@media (max-width: 640px) {
  .tracker-table {
    font-size: 8px;
  }
  .tracker-table th,
  .tracker-table td {
    padding: 2px !important;
  }
  .lesson-grid {
    width: 100%;
    margin: 2px 0;
  }
  .custom-panel {
    width: 100%;
    padding: 5px;
  }
  .chart-container {
    height: 150px !important;
  }
}

@media (min-width: 641px) and (max-width: 768px) {
  .tracker-table {
    font-size: 10px;
  }
  .tracker-table th,
  .tracker-table td {
    padding: 4px !important;
  }
  .lesson-grid {
    font-size: 12px;
  }
  .custom-panel {
    width: 90%;
    padding: 10px;
  }
  .chart-container {
    height: 250px !important;
  }
}

@media (min-width: 769px) {
  .tracker-table {
    font-size: 14px;
  }
  .tracker-table th,
  .tracker-table td {
    padding: 8px !important;
  }
  .lesson-grid {
    font-size: 14px;
  }
  .chart-container {
    height: 400px !important;
  }
}